
.container {
    padding: 0;
    width: 100%;
    height: 100%;
    
}
.interface {
    flex: 1;
}

.listContainer {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-shrink: 1;
    transition: width 0.25s ease-in-out;
    position: relative;
}
.showListContainer{
    width:100%;
}
.hideListContainer{
    width:0%;
}

:global(.Resizer.vertical) {
    width: 10px;
    cursor: col-resize;
    z-index: 1;
    background-color: #ffffff;
}